.navbar {
	background-color: #060b26;
	height: 80px;
	display: flex;
	justify-content: flex-end;
	padding-right: 20px;
	align-items: center;
}
.menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
}
.nav-menu {
	background-color: #060b26;
	width: 250px;
	height: 100vh;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 1000ms;
}
.nav-menu.active {
	left: 0;
	transition: 500ms;
	background-color: #1a83ff;
}
.nav-text {
	display: flex;
	justify-content: start;
	align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}
.nav-text a {
	text-decoration: none;
	color: aliceblue;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}
.nav-text a:hover {
	background-color: #060b26;
}
.nav-menu-items {
	width: 100%;
}
.navbar-toggle {
	background-color: #060b26;
	width: 100%;
	height: 80px;
	display: flex;
	justify-content: start;
	align-items: center;
}
span {
	margin-left: 16px;
}
